.logo {

	img {

		height: inherit;
	}
}

@media (max-width: 1199.98px){
	
	.logo {

		img {

			max-width: 200px;
		}
	}
}

@media (max-width: 991.98px){
	
	.logo {

		img {

			max-width: 170px;
		}
	}
}

@media (max-width: 767.98px){
	
	.logo {

		img {

			max-width: 150px;
		}
	}
}

@media (max-width: 575.98px){
	
	.logo {

		img {

			max-width: 130px;
		}
	}
}

@media (max-width: 480.98px){
	
	.logo {

		img {

			max-width: 120px;
		}
	}
}

@media (max-width: 374px){
	
	.logo {

		img {
			
			max-width: 110px;
		}
	}
}

@media (max-width: 350px){

	.logo {

		img {
			
			max-width: 100px;
		}
	}
	
}