/* global
===================================================*/
@font-face {
  font-family: "Rubik";
  src: url("../fonts/rubik-v14-latin_cyrillic-regular.woff2") format("woff2"), url("../fonts/rubik-v14-latin_cyrillic-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Rubik";
  src: url("../fonts/rubik-v14-latin_cyrillic-500.woff2") format("woff2"), url("../fonts/rubik-v14-latin_cyrillic-500.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Rubik";
  src: url("../fonts/rubik-v14-latin_cyrillic-900.woff2") format("woff2"), url("../fonts/rubik-v14-latin_cyrillic-900.woff") format("woff");
  font-weight: 900;
  font-style: normal;
  font-display: swap;
}
/*============================================================================
	#Global
==============================================================================*/
html {
  box-sizing: border-box;
  height: 100%;
  scroll-behavior: smooth;
}

*,
*:before,
*:after {
  box-sizing: inherit;
  outline: none;
}

body {
  height: 100%;
  background: #262634;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

img {
  height: auto;
  max-width: 100%;
  vertical-align: top;
}

svg {
  vertical-align: top;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  appearance: none;
  margin: 0;
}

/* typography
===================================================*/
html {
  font-size: 100%;
}

body,
button,
input,
select,
optgroup,
textarea {
  color: #ffffff;
  font-family: "Franklin Gothic", sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.66;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
}

.typography > *:first-child {
  margin-top: 0;
}
.typography > *:last-child {
  margin-bottom: 0;
}

h1, .h1,
h2, .h2,
h3, .h3,
h3, .h3,
h4,
h5,
h6 {
  font-family: "Franklin Gothic", sans-serif;
  font-weight: 500;
  margin: 18px 0 12px;
  color: #ffffff;
}

h1, .h1 {
  font-size: 28px;
  line-height: 1.12;
}

h2, .h2 {
  font-size: 26px;
  line-height: 1.12;
}

h3, .h3 {
  font-size: 22px;
  line-height: 1.12;
}

@media (max-width: 767.98px) {
  h1, .h1 {
    font-size: 26px;
  }

  h2, .h2 {
    font-size: 22px;
  }

  h3, .h3 {
    font-size: 20px;
  }
}
@media (max-width: 374px) {
  h1, .h1 {
    font-size: 24px;
  }

  h2, .h2 {
    font-size: 20px;
  }

  h3, .h3 {
    font-size: 18px;
  }
}
p {
  margin: 14px 0 16px;
  line-height: 1.8;
}

a {
  text-decoration: none;
  color: #24BB9D;
  transition: color 0.4s cubic-bezier(0.075, 0.82, 0.165, 1);
}
a:hover {
  text-decoration: underline;
  color: #24BB9D;
}

strong {
  font-weight: 500;
}

ul, ol {
  list-style-type: none;
  margin: 0 0 16px 0;
  padding: 0 0 0 16px;
}
ul li, ol li {
  position: relative;
  margin: 14px 0 14px;
}
ul li h1, ul li h2, ul li h3, ul li h4, ul li h5, ul li h6, ol li h1, ol li h2, ol li h3, ol li h4, ol li h5, ol li h6 {
  display: inline-block;
  margin: 0;
  color: #ffffff;
  font-family: "Franklin Gothic", sans-serif;
  font-weight: 500;
}
ul strong, ol strong {
  font-weight: 500;
  color: inherit;
}

ul li {
  padding-left: 16px;
  position: relative;
}
ul li::before {
  position: absolute;
  content: "";
  display: inline-block;
  height: 4px;
  width: 4px;
  left: 0;
  top: calc(50% - 2px);
  border-radius: 100%;
  background: #c4c4c4;
}

ol {
  counter-reset: section;
}
ol li::before {
  counter-increment: section;
  content: counter(section) ". ";
  font-weight: 400;
  padding-right: 4px;
}

.table-responsive {
  margin: 24px 0;
}
.table-responsive__name {
  font-size: 14px;
  font-weight: 500;
  margin-bottom: 8px;
}
@media (max-width: 767.98px) {
  .table-responsive__name {
    font-size: 12px;
  }
}

table.table {
  width: 100%;
  border-collapse: collapse;
  position: relative;
  overflow: hidden;
  font-size: 14px;
  font-weight: 400;
  line-height: 140%;
  padding-bottom: 10px;
}
@media (max-width: 767.98px) {
  table.table {
    font-size: 12px;
  }
}
table.table::before {
  position: absolute;
  content: "";
  top: 0;
  width: 0;
  height: 100%;
  width: 100%;
  pointer-events: none;
}
table.table thead tr {
  background: #3D3D3D;
}
table.table thead tr:hover {
  background: #333333;
}
table.table th {
  text-align: left;
}
table.table tr {
  background: #3D3D3D;
  transition: all 0.8s cubic-bezier(0.075, 0.82, 0.165, 1);
  color: white;
  box-shadow: 0px 2.76883px 6.92208px rgba(0, 0, 0, 0.399312);
}
table.table tr:hover {
  background: #333333;
}
table.table tr:nth-child(even) {
  background: transparent;
  color: white;
}
table.table tr:nth-child(even):hover {
  background: rgba(0, 0, 0, 0.15);
}
table.table td, table.table th {
  padding: 11px 18px;
  text-align: left;
  border-right: 2px solid #262634;
}
table.table td:last-child, table.table th:last-child {
  border: none;
}
table.table--2col td:first-child {
  width: 30%;
}
@media (max-width: 767.98px) {
  table.table--2col td:first-child {
    width: 20%;
  }
}
@media (max-width: 480px) {
  table.table--2col td:first-child {
    width: auto;
  }
}
table.table--3col td:first-child {
  width: 20%;
}
@media (max-width: 480px) {
  table.table--3col td:first-child {
    width: auto;
  }
}
table.table--yellow td:last-child {
  text-align: right;
  color: #FFE41E;
}
@media (max-width: 575.98px) {
  .table-responsive {
    width: 100%;
    overflow-x: auto;
  }
  .table-responsive table.table {
    min-width: 540px;
    margin-bottom: 8px;
  }
  .table-responsive table.table--yellow {
    min-width: 100%;
  }
}
/* components
===================================================*/
.container {
  width: 100%;
  padding-right: 10px;
  padding-left: 10px;
  margin-right: auto;
  margin-left: auto;
}

@media (min-width: 375px) {
  .container {
    padding-right: 15px;
    padding-left: 15px;
  }
}
@media (min-width: 576px) {
  .container {
    max-width: 540px;
  }
}
@media (min-width: 768px) {
  .container {
    max-width: 720px;
  }
}
@media (min-width: 992px) {
  .container {
    max-width: 960px;
  }
}
@media (min-width: 1200px) {
  .container {
    max-width: 1120px;
  }
}
.header {
  padding: 16px 0 16px;
  background: linear-gradient(180deg, #424242 0%, #343434 100%);
  border: 1px solid #343550;
}
.header .container {
  display: grid;
  grid-auto-flow: column;
  justify-content: space-between;
  align-items: center;
  grid-template-columns: auto;
}
.header__logo {
  margin: 0 auto;
}
.header__reg-text {
  font-size: 19px;
  line-height: 1.3;
  text-align: right;
}
.header__auth {
  display: grid;
  grid-auto-flow: column;
  justify-content: flex-end;
  align-items: center;
  gap: 18px;
}

@media (max-width: 1199.98px) {
  .header {
    padding: 14px 0 14px;
  }
  .header__reg-text {
    font-size: 18px;
    line-height: 1.22;
  }
  .header__auth {
    gap: 16px;
  }
}
@media (max-width: 991.98px) {
  .header {
    padding: 12px 0 12px;
  }
  .header__reg-text {
    font-size: 17px;
  }
  .header__auth {
    gap: 14px;
  }
}
@media (max-width: 767.98px) {
  .header {
    padding: 10px 0 10px;
  }
  .header__top, .header__bottom {
    padding: 10px 0;
  }
  .header__reg-text {
    font-size: 14px;
  }
  .header__reg-text br {
    display: none;
  }
  .header__auth {
    gap: 12px;
  }
}
@media (max-width: 575.98px) {
  .header .container {
    grid-template-columns: auto;
    justify-content: space-between;
  }
  .header__reg-text {
    display: none;
  }
  .header__auth {
    gap: 10px;
    justify-content: flex-end;
  }
  .header__auth .button-primary--dark {
    display: none;
  }
}
@media (max-width: 480.98px) {
  .header__reg-text {
    font-size: 10px;
  }
  .header__auth {
    gap: 8px;
  }
}
@media (max-width: 374px) {
  .header {
    padding: 8px 0 8px;
  }
}
.main-menu {
  position: relative;
  z-index: 1;
  padding: 10px 0;
}
.main-menu__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.main-menu__list li {
  margin: 0;
  padding: 0;
}
.main-menu__list li::before {
  display: none;
}
.main-menu__link {
  padding: 15px 16px;
  display: flex;
  align-items: center;
  color: #A9A9A9;
  font-family: "Rubik", sans-serif;
  font-size: 30px;
  line-height: 1;
  font-weight: 400;
  transition: all 0.4s cubic-bezier(0.075, 0.82, 0.165, 1);
  position: relative;
  z-index: 1;
}
.main-menu__link img {
  margin-right: 8px;
  width: 34px;
  height: 34px;
}
.main-menu__link span {
  margin-top: 4px;
}
.main-menu__link:hover {
  text-decoration: none;
}
.main-menu__link.active {
  padding-left: 24px;
}
.main-menu__link.active::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  height: 12px;
  width: 12px;
  border-radius: 100%;
  background: #24BB9D;
  transform: translateY(-50%);
}

@media (max-width: 1199.98px) {
  .main-menu {
    padding: 8px 0;
  }
  .main-menu__link {
    font-size: 24px;
    padding: 12px 22px;
  }
  .main-menu__link.active {
    padding-left: 20px;
  }
  .main-menu__link.active::before {
    height: 8px;
    width: 8px;
  }
  .main-menu__link img {
    width: 32px;
    height: 32px;
  }
}
@media (max-width: 991.98px) {
  .main-menu__link {
    font-size: 20px;
    padding: 10px 14px;
  }
  .main-menu__link.active {
    padding-left: 16px;
  }
  .main-menu__link.active::before {
    height: 6px;
    width: 6px;
  }
  .main-menu__link img {
    width: 28px;
    height: 28px;
  }
}
@media (max-width: 767.98px) {
  .menu-section {
    padding: 0;
  }

  .main-menu {
    padding: 10px 0 6px;
  }
  .main-menu__list {
    white-space: nowrap;
    overflow-x: scroll;
    justify-content: space-around;
    padding-bottom: 4px;
  }
  .main-menu__link {
    font-size: 16px;
    padding: 8px 12px;
    flex-direction: column;
  }
  .main-menu__link.active {
    padding-left: 10px;
  }
  .main-menu__link.active::before {
    height: 4px;
    width: 4px;
  }
  .main-menu__link img {
    margin-right: 0;
    margin-bottom: 2px;
    width: 26px;
    height: 26px;
  }

  ::-webkit-scrollbar {
    -webkit-appearance: none;
  }

  ::-webkit-scrollbar:horizontal {
    height: 2px;
  }

  ::-webkit-scrollbar-thumb {
    background-color: #f1ceff;
  }

  ::-webkit-scrollbar-track {
    background-color: transparent;
  }
}
@media (max-width: 575.98px) {
  .menu-section {
    padding: 0;
    margin-bottom: 10px;
  }

  .main-menu__list {
    justify-content: space-between;
  }
  .main-menu__link img {
    width: 24px;
    height: 24px;
  }
}
.front-slider {
  position: relative;
  z-index: 1;
  margin: 0 0 16px 0;
}
.front-slider__item {
  display: flex;
  justify-content: center;
  width: 100%;
  transition: all 0.8s cubic-bezier(0.075, 0.82, 0.165, 1);
  border-radius: 10px;
  overflow: hidden;
}
.front-slider__item:hover {
  box-shadow: 0 0 0 2px #24BB9D;
}
.front-slider__item img {
  width: 100%;
  height: 100%;
  display: block;
  object-fit: cover;
}

@media (max-width: 1199.98px) {
  .front-slider {
    margin: 0 0 16px 0;
  }
  .front-slider__item {
    border-radius: 9px;
  }
}
@media (max-width: 991.98px) {
  .front-slider {
    margin: 0 0 14px 0;
  }
  .front-slider__item {
    border-radius: 8px;
  }
}
@media (max-width: 575.98px) {
  .front-slider__item picture {
    width: 100%;
  }
}
@media (max-width: 480.98px) {
  .front-slider__item {
    border-radius: 6px;
  }
}
.section {
  margin-bottom: 40px;
}
.section__header {
  margin-bottom: 30px;
  display: flex;
  justify-content: center;
  position: relative;
  z-index: 1;
  background: linear-gradient(180deg, #424242 0%, #353535 100%);
  box-shadow: 0px 1.98261px 7.93043px rgba(0, 11, 108, 0.25);
  border-radius: 9px;
  padding: 11px 0 11px;
}
.section__title {
  text-align: center;
  font-size: 30px;
  font-family: "Rubik", sans-serif;
  font-weight: 400;
  line-height: 1.2;
  margin: 0;
  color: #A9A9A9;
  padding: 0 20px;
}
.section--top-games {
  margin: 0;
  padding: 0;
}
.section--top-games .section__header {
  margin: 0;
}
.section--typography {
  padding: 0px 0 60px;
}
@media (max-width: 1199.98px) {
  .section__header {
    margin-bottom: 25px;
    border-radius: 8px;
  }
  .section__title {
    font-size: 26px;
  }
}
@media (max-width: 991.98px) {
  .section__header {
    margin-bottom: 22px;
    border-radius: 7px;
  }
  .section__title {
    font-size: 22px;
